import React from 'react';
import PropTypes from 'prop-types';
import './Movie.css';

function Movie({ year, genres, title, summary, poster, trailer }) {
    return (
        <div className="movie">
            <img className="poster" src={poster} alt={title} title={title} />
            <h3 className="title">{title}</h3>
            <h5 className="year">{year} |</h5>
            <ul className="genres">
                {genres.map((genre, index) => (
                    <li className="genre" key={index}>{genre}</li>
                    ))}
            </ul>
            <p className="summary">{summary.length > 300 ? summary.slice(0, 300) + "..." : summary}</p>
            {trailer
                ? <a className="link" href={"https://www.youtube.com/watch?v=" + trailer} target="_blank" rel="noopener noreferrer"> </a>
                : null
            }
        </div>
    );
}

Movie.propTypes = {
    id: PropTypes.number.isRequired,
    year: PropTypes.number.isRequired,
    genres: PropTypes.arrayOf(PropTypes.string).isRequired,
    title: PropTypes.string.isRequired,
    summary: PropTypes.string.isRequired,
    poster: PropTypes.string.isRequired,
    trailer: PropTypes.string
};

export default Movie;